import * as React from 'react';
import {Button} from 'reactstrap';
import {Tag} from './types'
///TODO disable button click
interface IProps {
      tag:Tag
}

interface IState {
  buttonState?: boolean;
}

export class IndicatorButton extends React.Component<IProps,IState>{
      constructor(props:IProps) {
            super(props);
            this.state ={buttonState : this.props.tag.value};
      }
                  
   render(){
      return(
      <Button id ={this.props.tag.name} color="secondary">{this.props.tag.name}</Button>
            )
      } 
}