import * as React from "react";
import * as ReactDOM from "react-dom";
import {Component_container} from "./components/component_container";
import {NewContainer} from './components/newcontainer'

var xhttp = new XMLHttpRequest();
//var url1 = 'http://www.json-generator.com/api/json/get/cdWUfSHZNK?indent=2'
var url = 'http://192.168.1.111:8000/tag/latest'

xhttp.onreadystatechange = function() {
if (xhttp.readyState === 4) {  
    if (xhttp.status === 200) {  
        var data:any = JSON.parse(xhttp.responseText)
        console.log(data)
        ReactDOM.render(
          <NewContainer data={data}/>,
          document.getElementById("container")
        );
    } else {  
        console.log(xhttp.statusText)
    }  
}  
};

xhttp.open("GET", url, true);
xhttp.send(); 