import * as React from 'react';
import ReactSpeedometer from 'react-d3-speedometer';

export class Speedometer extends React.Component{
   render(){
      return(
              <div>
                <ReactSpeedometer
                maxValue={100}
                value={47}
                needleColor="red"
                startColor="green"
                segments={10}
                endColor="blue"/>
              </div>
            )
      } 
}
