import * as React from 'react';
import {Button} from 'reactstrap';
import {callbackfun,Tag} from './types'

interface IProps {
      tag:Tag
      updateCallback:callbackfun
    }
    
    
interface IState {
      buttonState:boolean
}

export class MomentaryButton extends React.Component<IProps,IState>{
      constructor(props:IProps) {
            super(props);
            this.state ={buttonState:this.props.tag.value};

            this.buttonClick= this.buttonClick.bind(this);
      }
      
      buttonClick()  {
            this.setState(state =>({
                  buttonState: !state.buttonState
            }));
      }
          

      
   render(){
      return(
      <Button id ={this.props.tag.name} color="secondary" onClick={this.buttonClick}>{this.props.tag.name}</Button>
            )
      } 
}