import * as React from 'react';
import {Row,Button,FormGroup,Label,Input} from 'reactstrap';
import { callbackfun } from './types';
import {Tag } from './types';


interface IProps {
  tag:Tag
  updateCallback:callbackfun
}


  interface IState {
    value:any;
  }
  
export class InputField extends React.Component<IProps,IState> {
      constructor(props:IProps) {
        super(props);
        this.state = {value:this.props.tag.value};
    
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
      }
    
      handleChange(event: React.FormEvent<EventTarget>) {
        let target = event.target as HTMLInputElement;
        this.setState({value:target.value});
      }
    
      handleSubmit(event: React.FormEvent<EventTarget>) {
        //alert('A name was submitted: ' + this.state.value);
        event.preventDefault();
        this.props.updateCallback(this.props.tag.name, this.state.value)
      }
    
      render() {
        return (
          <form onSubmit={this.handleSubmit}>
            <label>
              UZER_HZ:
              <input type="text" value={this.state.value} onChange={this.handleChange} />
            </label>
            <input type="submit" value="Submit" />
          </form>
        );
      }
    }