import * as React from "react";
import {select,line,axisBottom,scaleLinear,axisLeft,curveCardinal,scaleTime,area} from 'd3';
import { values } from "lodash";

interface Types{
  date:Number;
  value:Number;
}

export const Line_chart:React.FC =()=>{
  const [data,setData] = React.useState([ {date:new Date("2020-08-31 00:00:00"),value: 93.24},
                                          {date:new Date("2020-08-31 01:00:00"),value: 95.35},
                                          {date:new Date("2020-08-31 02:00:00"),value: 98.84},
                                          {date:new Date("2020-08-31 03:00:00"),value: 99.92},
                                          {date:new Date("2020-08-31 04:00:00"),value: 99.80},
                                          {date:new Date("2020-08-31 05:00:00"),value: 99.47}]);
  const svgRef = React.useRef();
  const ha = React.useRef();

  React.useEffect(()=>{
      const svg = select(svgRef.current)
      const xScale = scaleTime().domain([new Date("2020-08-31 00:00:00"), new Date("2020-08-31 10:00:00")]).range([0,400]);
      const yScale = scaleLinear().domain([90,100]).range([250,0]);

      const yAxis =axisLeft(yScale)

            svg.select(".y-axis")
                .attr("transform","translate(30,10)")
                .call(yAxis);
      
      const xAxis =axisBottom(xScale)

            svg.select(".x-axis")
                .attr("transform","translate(30,260)")
                .call(xAxis);          

      const myLine = line<any>().x((d)=> {return xScale(d.date)})
                            .y((d)=>{return yScale(d.value)})
                            .curve(curveCardinal)
            
            svg.selectAll(".line")
                .data([data]) 
                .join('path')
                .attr("class","line")
                .attr("transform","translate(30,10)")
                .attr("d",myLine(data))
                .attr("fill", "none")
                .attr('stroke','blue');
  },[data])
    
            
 
    return(
      <React.Fragment>
       <svg ref={svgRef} overflow="visible" height="300" width="400" fill="red" stroke="dark" >
         <g ref={ha} className ="y-axis"/>
         <g className ="x-axis"/>
        </svg>
       </React.Fragment>
  )
}