import * as React from "react";
import {Speedometer} from './speedometer';
import BarChart from './time_chart';
import {Line_chart} from './line_chart';
import { Container, Row, Col, Button } from 'reactstrap'; 
import { MomentaryButton } from "./momentarybutton";
import { IndicatorButton } from "./idicatorbutton";
import { InputField } from "./inputfield";
import {Tag} from "./types"

interface IProps {
  data:any
}

interface IState {
    VFD_STOP:Tag
    VFD_FORWARD:Tag
    VFD_REVERSE:Tag
    VFD_START_FRD:Tag
    VFD_START_REV:Tag
    VFD_OFF:Tag
    USER_HZ:Tag
    USER_REAL_HZ:Tag
}

function tagFactory(tag_data:any){
  console.log(tag_data)
    var tag:Tag = {
        name: String(tag_data.name),
        path: String(tag_data.path),
        health: String(tag_data.health),
        last_updated: new Date(),
        value: tag_data.value
    }
    return tag
}

export class NewContainer extends React.Component<IProps, IState>{
    constructor(props:IProps) {
        super(props)
        const data = this.props.data
        console.log("In constructor")
        console.log(data.tags)
        this.state = ({
            VFD_STOP:tagFactory(data.tags.VFD_STOP),
            VFD_START_FRD:tagFactory(data.tags.VFD_START_FRD),
            VFD_START_REV:tagFactory(data.tags.VFD_START_REV),
            VFD_FORWARD:tagFactory(data.tags.VFD_FORWARD),
            VFD_REVERSE:tagFactory(data.tags.VFD_REVERSE),
            VFD_OFF:tagFactory(data.tags.VFD_OFF),
            USER_HZ:tagFactory(data.tags.USER_HZ),
            USER_REAL_HZ:tagFactory(data.tags.VFD_REAL_HZ)
      })
    }
  stateUpdater(data:any){
      this.setState({
            VFD_STOP:data.tags.VFD_STOP,
            VFD_START_FRD:data.tags.VFD_START_FRD,
            VFD_START_REV:data.tags.VFD_START_REV,
            VFD_FORWARD:data.tags.VFD_FORWARD,
            VFD_REVERSE:data.tags.VFD_REVERSE,
            VFD_OFF:data.tags.VFD_OFF,
            USER_HZ:data.tags.USER_HZ
      })
  }

  tag_updater(tagname:String, value:any){
      console.log(tagname, value)
      this.setState({VFD_STOP:tagFactory({
            "name": "VFD_Tada",
            "path": "Channel3.Device1.VFD_STOP",
            "value": false,
            "health": "Good",
            "last_updated": "2020-09-02T12:03:36.923Z"
      })})
  }
  render(){

    return(
    <Container style={{backgroundColor: '#333333'}} className="pt-2">
      <Row style={{backgroundColor: '#e1e1ed'}}className="d-flex justify-content-center pt-5 pb-5 mx-2 mt-4 mb-4">
        <Button id ="user_hz" color="secondary">USER_HZ</Button>
        <MomentaryButton
        tag={this.state.VFD_STOP} updateCallback={this.tag_updater.bind(this)}/>
        <MomentaryButton
        tag={this.state.VFD_START_FRD} updateCallback={this.tag_updater.bind(this)}/>
        <MomentaryButton
        tag={this.state.VFD_START_REV} updateCallback={this.tag_updater.bind(this)}/>
        <IndicatorButton
        tag={this.state.VFD_FORWARD}
        />
        <IndicatorButton
        tag={this.state.VFD_REVERSE}
        />
        <IndicatorButton
        tag={this.state.VFD_OFF}
        />
        <InputField tag={this.state.USER_HZ} updateCallback={this.tag_updater.bind(this)}></InputField>
      </Row>
      <Row>
        <Col style={{padding:'30px',backgroundColor: '#e1e1ed'}} className="ml-4 mb-4">
          <Line_chart/>
        </Col>
        <Col style={{padding:'50px',backgroundColor: '#e1e1ed'}} className="d-flex justify-content-center ml-4 mr-4 pt-5 mb-4">
          <Speedometer/>
        </Col>
      </Row>
    </Container> 
  )
}
}